* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  padding: 40px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: 'Montserrat', sans-serif;
  background-image: url(../images/wood-bg.jpg);

  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

@media (max-width: 795px) {
  body {
    padding: 0;
  }
}

.shadow-bg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 330%;

  background-color: rgba(0, 0, 0, 0.6);

  z-index: -1;
}

/* @media (max-width: 520px) {
  input,
  select {
    width: 80%;
  }
} */

.wrapper {
  width: 800px;
  min-height: 955px;
  box-shadow: 3px 4px 26px 22px rgba(0, 0, 0, 0.7);
  -webkit-box-shadow: 3px 4px 26px 22px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 3px 4px 26px 22px rgba(0, 0, 0, 0.7);
}

.header {
  position: relative;
  height: 360px;
  padding: 20px 40px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  flex-direction: column;
  font-family: 'Dancing Script', cursive;
  background-image: url('../images/background.jpg');
  background-size: cover;
  background-position: center;
  z-index: 0;
}

@media (max-width: 716px) {
  .header {
    align-items: center;
  }
}

.shadow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  z-index: -1;
}

.bottom-shadow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: -1;
}

h1 {
  margin-bottom: 30px;
  font-size: 50px;
}

a {
  text-decoration: none;
  color: #dddada;
}

.search {
  padding: 10px 20px;
  margin-bottom: 15px;
  width: 350px;
  font-size: 16px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.7);
  outline: none;
  border: none;
}

@media (max-width: 400px) {
  .search {
    width: 80%;
    font-size: 14px;
  }
}

.interface {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 716px) {
  .interface {
    margin-left: 20px;
    width: 80%;
    flex-direction: column-reverse;
    align-items: start;
    gap: 10px;
  }
}

.button-interface {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.add-drink {
  display: flex;
  padding: 0 20px 0 15px;
  align-items: center;
  justify-content: space-between;
  width: 150px;
  height: 39px;
  border: none;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.75);
  font-size: 15px;
  border-radius: 5px;
  color: rgb(93, 93, 93);
}

.add-drink:hover > .fa-plus {
  transform: scale(1.4);
}

.deleted-drinks:hover > .fa-trash {
  transform: scale(1.3);
}

.fa-plus {
  scale: 1.3;
  transition: transform 0.2s ease;
}

.fa-trash {
  scale: 0.9;
  transition: transform 0.2s ease;
}

.deleted-drinks {
  display: none;
  padding: 5px;
  width: 30px;
  height: 39px;
  border: none;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 5px;
  color: rgb(93, 93, 93);
}

.clear-storage {
  padding: 5px;
  width: 30px;
  height: 39px;
  border: none;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 5px;
  color: rgb(93, 93, 93);
}

.clear-storage:hover > .fa-rotate-right {
  transform: scale(1.4);
}

.fa-rotate-right {
  transition: transform 0.2s ease;
}

.concrete-bg {
  position: relative;
  min-height: 597px;
  background-image: url(https://as2.ftcdn.net/v2/jpg/02/50/21/85/1000_F_250218533_ugSnqRo9CPm1DyZ8jWwwOFQtmKdKjhZT.jpg);
  background-size: fill;
  background-position: 25% 0;
  z-index: 0;
  box-shadow: 1px 33px 36px -4px rgba(0, 0, 0, 0.73) inset;
  -webkit-box-shadow: 1px 33px 36px -4px rgba(0, 0, 0, 0.73) inset;
  -moz-box-shadow: 1px 33px 36px -4px rgba(0, 0, 0, 0.73) inset;
}

/* @media (max-width: 540px) {
  ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
  }
} */

.drink-list {
  padding: 20px 40px 50px 40px;
}
@media (max-width: 460px) {
  .drink-list {
    /* width: 80%;
    padding: 20px 0px; */
  }
}

h2 {
  padding: 30px 0;
}
@media (max-width: 480px) {
  h2 {
    text-align: center;
  }
}

.drink-list li {
  display: flex;
  justify-content: space-between;
  min-width: 300px;
  max-height: 36px;
  padding: 8px 30px;
  border-bottom: 1px solid rgb(226, 226, 226, 0.6);
  transition-property: font-size;
  transition-duration: 250ms;
  transition-timing-function: ease;
}

@media (min-width: 1280px) {
  .drink-list li:hover {
    align-items: center;
    cursor: pointer;
    font-size: 24px;
  }
}

@media (max-width: 460px) {
  .drink-list li {
  }
}

@media (min-width: 1280px) {
  .drink-list li:hover i {
    font-size: 18px;
  }
}

.drink-list i {
  padding: 0px 25px;
  display: none;
}

@media (max-width: 1280px) {
  .drink-list i {
    display: block;
    opacity: 0.35;
  }
}

.drink-list li:last-child {
  border-bottom: 0px;
}

@media (min-width: 1280px) {
  .fa-square-minus {
    padding: 8px 25px;
    opacity: 0.25;
    transition: scale 0.3s;
  }
}

@media (min-width: 1280px) {
  .fa-square-minus:hover {
    opacity: 1;
    scale: 1.35;
  }
}

.add-drink-modal {
  position: fixed;
  top: 30%;
  width: 600px;
  height: 410px;
  display: none;
  align-items: center;
  padding: 40px 40px;
  flex-direction: column;
  gap: 20px;
  background-image: url(https://cdn.pixabay.com/photo/2017/01/07/20/41/alcohol-1961542_960_720.jpg);
  border-radius: 8px;
  overflow: hidden;
  background-position: 40%;
  background-attachment: fixed;
  animation: appear 0.5s;
  transition: opacity 0.5s;
}

@media (max-width: 650px) {
  .add-drink-modal {
    width: 90%;
  }
}

.modal-shadow {
  display: none;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  animation: appear 0.5s;
  transition: opacity 0.5s;
}

.add-name,
.add-description {
  text-align: center;
  font-size: 24px;
  font-weight: 500;
  color: #e7e5e5;
}

.add-drink-input {
  border-radius: 5px;
  padding: 10px;
  width: 80%;
  outline: none;
  border: none;
  background-color: rgba(255, 255, 255, 0.7);
}

.add-textarea {
  border-radius: 5px;
  padding: 15px;
  width: 80%;
  outline: none;
  border: none;
  background-color: rgba(255, 255, 255, 0.7);
}

.submit {
  padding: 5px 40px;
  border: none;
  border-radius: 5px;
  margin-top: 4px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.7);
  font-size: 15px;
  color: #3c3c3c;
}

.span-popup {
  display: none;
  color: rgb(255, 74, 74);
  font-size: 18px;
}

.description-modal {
  position: fixed;
  transform: translateY(-50%);
  top: 50%;
  display: none;
  padding: 40px 20px 40px 20px;

  width: 600px;
  min-height: 200px;
  background-image: url(https://media.istockphoto.com/id/525108931/photo/design-of-dark-wood-background.jpg?b=1&s=170667a&w=0&k=20&c=MmYXPZIOizJ7ZAvgRiiiOluCWaI1XN6ng0Vv7e_Mfw0=);
  border-radius: 8px;
  overflow: hidden;
  background-position: 100%;

  object-fit: fill;
  transition: opacity 0.5s;
  animation: appear 0.5s;
}

@media (max-width: 650px) {
  .description-modal {
    width: 90%;
  }
}

.description {
  width: 100%;
  height: 100%;
  display: flex;
  text-align: center;
  border-radius: 8px;
  font-weight: 600;
  line-height: 1.7;
  font-size: 18px;
  font-style: italic;
  color: #fff;
}

.deleted-drinks-modal {
  position: fixed;
  top: 25%;
  width: 600px;
  min-height: 410px;
  display: none;
  align-items: center;
  padding: 15px 40px;
  flex-direction: column;
  gap: 20px;
  background-image: url(https://cdn.pixabay.com/photo/2016/11/19/18/46/concrete-1840731_960_720.jpg);
  border-radius: 8px;
  overflow: hidden;
  background-position: 40%;
  background-attachment: fixed;
  animation: appear 0.5s;
  transition: opacity 0.5s;
}

@media (max-width: 650px) {
  .deleted-drinks-modal {
    width: 90%;
    padding: 40px 5px;
  }
}

.deleted-drink {
  display: flex;
  width: 100%;
  gap: 30px;
  justify-content: space-between;
  padding: 7px 50px 4px 50px;
  font-size: 20px;
  border-bottom: 1px solid black;
}

@media (max-width: 550px) {
  .deleted-drink {
    padding: 0;
  }
}

.save {
  padding: 5px;
  margin-left: 0px;
  background: transparent;
  border: none;
  scale: 1.5;
  cursor: pointer;
}

@media (max-width: 650px) {
  .save {
    scale: 1.2;
  }
}

.delete-permament {
  padding: 5px;
  margin-left: 20px;
  background: transparent;
  border: none;
  scale: 1.5;
  cursor: pointer;
}

@media (max-width: 650px) {
  .delete-permament {
    scale: 1.2;
    margin-left: 5px;
  }
}

.delete-popup-div {
  position: fixed;
  transform: translateY(-50%);
  top: 50%;
  width: 500px;
  min-height: 200px;
  display: none;
  align-items: center;
  padding: 15px 20px;
  flex-direction: column;
  justify-content: center;
  gap: 40px;
  background-image: url(https://cdn.pixabay.com/photo/2017/06/26/12/49/red-wine-2443699_960_720.jpg);
  background-size: cover;
  border-radius: 8px;
  overflow: hidden;
  animation: appear 0.5s;
  transition: opacity 0.5s;
}

@media (max-width: 650px) {
  .delete-popup-div {
    text-align: center;
    width: 80%;
  }
}

.delete-popup-text {
  font-size: 25px;
  font-weight: bold;
  text-align: center;
  text-decoration: underline;
  color: #ffffff;
  text-decoration-color: rgb(148, 2, 2);
  text-decoration-thickness: 2px;
}

.popup-buttons-wrapper {
  display: flex;
  gap: 50px;
}

@media (max-width: 400px) {
  .popup-buttons-wrapper {
    gap: 30px;
  }
}

.delete-popup-cancel,
.delete-popup-remove {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  width: 120px;
  border: none;
  border-radius: 5px;
  cursor: pointer;

  font-size: 18px;
  color: #3c3c3c;
}

@media (max-width: 400px) {
  .delete-popup-cancel,
  .delete-popup-remove {
    width: 100px;
  }
}

.information {
  margin-top: 10px;
  padding: 0 20px;
  max-height: 100%;
  font-style: italic;
  font-size: 12px;
  min-width: 320px;
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
