.add-drink {
  width: 150px;
  height: 39px;
  cursor: pointer;
  color: #5d5d5d;
  background-color: rgba(255, 255, 255, .75);
  border: none;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px 0 15px;
  font-size: 15px;
  display: flex;
}

.add-drink:hover > .fa-plus {
  transform: scale(1.4);
}

.deleted-drinks:hover > .fa-trash {
