  color: #5d5d5d;
  background-color: rgba(255, 255, 255, .75);
  border: none;
  border-radius: 5px;
  padding: 5px;
  display: none;
}

.clear-storage {
  width: 30px;
  height: 39px;
  cursor: pointer;
  color: #5d5d5d;
  background-color: rgba(255, 255, 255, .75);
  border: none;
  border-radius: 5px;
  padding: 5px;
}

.clear-storage:hover > .fa-rotate-right {
  transform: scale(1.4);
}

.fa-rotate-right {
  transition: transform .2s;
